    <!-- Dialog instrucoes para assinatura -->
    <v-dialog v-model="dialogAssinatura" width="520" max-width="94%">
      <v-card>
        <v-card-title>Nova assinatura</v-card-title>
        <v-card-text>Envie uma foto da sua assinatura em uma folha de papel branca. No deixe aparecer nada alm do papel, por exemplo a mesa. No pode haver sombras, no tire foto escura, no use FLASH.</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="primary" text @click="dialogAssinatura = false">Cancelar</v-btn>
          <v-btn color="primary" @click="selecionarNovaAssinatura">Prosseguir</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <!-- File para upload de assinatura -->
    <input ref="file-assinatura" type="file" accept="image/jpeg, image/png" class="d-none" @change="updateAssinatura">
    <!-- Dialog para realizar rasterizao da assinatura -->
    <v-dialog v-model="dialogAssinaturaRaster">
      <v-card>
        <v-card-title>Remover fundo</v-card-title>
        <v-card-subtitle>Ajude-nos a remover o papel. Ajuste a potncia do apagador at restar somente a assinatura e o fundo branco.</v-card-subtitle>
        <v-card-text>
          <v-img :src="iptAssinaturaTmpRaster" contain width="100%" max-height="400"></v-img>
          <p class="subtitle-2 mb-0 mt-2">Potncia do apagador</p>
          <v-slider :min="-25" :max="-1" v-model="iptRasterLevel" prepend-icon="mdi-eraser" :step="3"></v-slider>
        </v-card-text>
        <v-card-actions class="justify-center">
          <v-btn color="primary" @click="() => {iptAssinatura = iptAssinaturaTmpRaster; dialogAssinaturaRaster = false;}">Confirmar</v-btn>
          <v-btn color="primary" @click="dialogAssinaturaRaster = false" outlined>Cancelar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <!-- [Mobile only] Dialog para escolher a origem da foto nova -->
    <v-dialog v-model="dialogMobileFoto" width="420" max-width="94%">
      <v-card>
        <v-card-title>Escolha a origem</v-card-title>
        <v-card-text>
          <v-btn block color="primary" outlined class="mb-3" @click="updateFotoMobile(false)">
            <v-icon>mdi-camera</v-icon> Cmera
          </v-btn>
          <v-btn block color="amber" outlined @click="updateFotoMobile(true)">
            <v-icon>mdi-image-multiple</v-icon> Galeria
          </v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>

    <!-- [Mobile only] Dialog para escolher a origem da assinatura nova -->
    <v-dialog v-model="dialogMobileAssinatura" width="420" max-width="94%">
      <v-card>
        <v-card-title>Escolha a origem</v-card-title>
        <v-card-text>
          <v-btn block color="primary" outlined class="mb-3" @click="updateAssinaturaMobile(false)">
            <v-icon>mdi-camera</v-icon> Cmera
          </v-btn>
          <v-btn block color="amber" outlined @click="updateAssinaturaMobile(true)">
            <v-icon>mdi-image-multiple</v-icon> Galeria
          </v-btn>
        </v-card-text>
      </v-card>
    </v-dialog>

    <!-- Dialog carregando informaes do CEP -->
    <v-dialog
      v-model="dialogLoadingCep"
      hide-overlay
      persistent
      width="300"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Rastreando CEP...
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
    <!-- Dialog Loading -->
    <v-dialog
      v-model="dialogLoading"
      width="300"
      persistent
      no-click-animation
      transition="none"
    >
      <v-card
        color="primary"
        dark
      >
        <v-card-text>
          Por favor aguarde
          <v-progress-linear
            indeterminate
            color="white"
            class="mb-0"
          ></v-progress-linear>
        </v-card-text>
      </v-card>
    </v-dialog>
  </async-container>
</template>

<script>
import AsyncContainer from '@/components/AsyncContainer';
import AppWebClient from '@/http/AppWebClient';
import StringHelper from '@/helpers/StringHelper';
import moment from 'moment';
import CepWebClient from '@/http/CepWebClient';
import {loadImageAsync} from '@/plugins/loadImage';
import * as faceapi from 'face-api.js';
import ColorThief from 'colorthief/dist/color-thief.mjs';
import ImageHelper from '@/helpers/ImageHelper';
import {config as appconfig} from '@/config';
export default {
name: 'Recadastramento',
  components: {AsyncContainer},
  data: () => ({
    loading: true,
    termo: null, // {id: Number, titulo: String, text: String}
    termoOk: false,
    dadosEssenciaisOk: false,
    stepper: 1,
    rules: {
      required: v => (typeof v === 'string' ? !!v.trim() : !!v) || 'Esta informao  obrigatria',
      email: v => /.+@.+\..+/.test(v) || 'Insira um E-mail vlido',
      phone: v => v.replace(/\D/g,'').length >= 10 || 'Complete o nmero do telefone',
      celphone: v => (v.replace(/\D/g,'').length >= 11 && v.replace(/\D/g,'').substr(2, 1) === '9') || 'Nmero de celular invlido',
    },
    msg_correcao: '',
    iptTermoAceite: false,
    savingAssinaturaTermo: false,

    //Dados para conferencia:
    iptCpf: '',
    iptNome: '',
    iptNpa: '',
    iptDataInscricao: '',
    iptFormacao: 1,
    iptDataNascimento: '',
    iptRgNumero: '',
    iptRgEmissor: '',
    iptRgUf: '',
    iptRgDataEmissao: '',
    iptNacionalidade: '',
    iptNaturalidade: '',
    iptNaturalidadeUf: '',

    iptEmails: [''],
    iptTelefones: [],

    iptCep: '',
    iptUf: '',
    iptCidade: '',
    iptBairro: '',
    iptLogradouro: '',
    iptNumero: '',
    iptComplemento: '',
    iptComercial: false,
    iptComercialCep: '',
    iptComercialUf: '',
    iptComercialCidade: '',
    iptComercialBairro: '',
    iptComercialLogradouro: '',
    iptComercialNumero: '',
    iptComercialComplemento: '',
    iptCorrespondencia: 1, //1 = Residencial, 2 = Comercial
    dialogLoadingCep: false,

    iptPai: '',
    iptMae: '',
    iptEstadoCivil: 1,
    iptEstadoCivilItems: [
      {value: 1, text: 'Solteiro'},
      {value: 2, text: 'Casado'},
      {value: 3, text: 'Vivo'},
      {value: 4, text: 'Separado'},
      {value: 5, text: 'Separado judicialmente'},
      {value: 6, text: 'Divorciado'},
      {value: 7, text: 'Unio estvel'},
      {value: 8, text: 'Desquitado'},
    ],
    iptDoador: 'N',
    iptSexo: 'M',
    iptSexoItems: [
      {value: 'M', text: 'Masculino'},
      {value: 'F', text: 'Feminino'},
      {value: 'O', text: 'Outros'},
    ],

    iptFoto: null, //NULL = Ainda no decidiu. Base64 = Nova foto.
    iptFotoCiente: false,

    iptAssinatura: null,
    iptAssinaturaTmp: null, //Assinatura do upload, sem rasterizao (original).
    iptAssinaturaTmpRaster: null, //Assinatura do upload, com rasterizao.
    iptRasterLevel: -16,

    dialogDadosRecusados: false,
    dialogRegrasFoto: false,
    dialogCrop: false,
    dialogAssinatura: false,
    dialogAssinaturaRaster: false,

    dialogMobileFoto: false,
    dialogMobileAssinatura: false,

    dialogLoading: false,
  }),
  methods: {
    async loadData() {
      const appWebClient = new AppWebClient();
      try {
        //Alimenta o formulario de dados sensiveis
        this.iptCpf = this.$store.getters['session/payload'].cpf;
        this.iptNome = this.$store.state.ficha.cadastro.nome;
        this.iptNpa = this.$store.state.ficha.cadastro.npa ? this.$store.state.ficha.cadastro.npa : '';

        const ficha = await appWebClient.getFichaCompleta();
        this.msg_correcao = ficha.msg_correcao;

        this.iptDataInscricao = ficha.data_inscricao;
        this.iptFormacao = ficha.formacao;
        this.iptDataNascimento = ficha.data_nascimento;
        this.iptRgNumero = ficha.rg_numero;
        this.iptRgEmissor = ficha.rg_emissor;
        this.iptRgUf = ficha.rg_uf;
        this.iptRgDataEmissao = ficha.rg_data;
        this.iptNacionalidade = ficha.nacionalidade;
        this.iptNaturalidade = ficha.naturalidade;
        this.iptNaturalidadeUf = ficha.naturalidade_uf;

        //Verifica se precisa assinar termo
        this.termoOk = await appWebClient.getPoliticaAssinei();
        if (!this.termoOk) this.termo = await appWebClient.getPoliticaTermoAtual();

        //Alimenta o formulario de contato
        this.iptEmails = ficha.emails;
        this.iptTelefones = ficha.telefones;

        //Alimenta o formulario de endereo
        this.iptCep = ficha.endereco.residencial.cep;
        this.iptUf = ficha.endereco.residencial.uf;
        this.iptCidade = ficha.endereco.residencial.cidade;
        this.iptBairro = ficha.endereco.residencial.bairro;
        this.iptLogradouro = ficha.endereco.residencial.logradouro;
        this.iptNumero = ficha.endereco.residencial.numero;
        this.iptComplemento = ficha.endereco.residencial.complemento;
        if (ficha.endereco.comercial && ficha.endereco.comercial.cep) {
          this.iptComercial = true;
          this.iptComercialCep = ficha.endereco.comercial.cep;
          this.iptComercialUf = ficha.endereco.comercial.uf;
          this.iptComercialCidade = ficha.endereco.comercial.cidade;
          this.iptComercialBairro = ficha.endereco.comercial.bairro;
          this.iptComercialLogradouro = ficha.endereco.comercial.logradouro;
          this.iptComercialNumero = ficha.endereco.comercial.numero;
          this.iptComercialComplemento = ficha.endereco.comercial.complemento;
        } else {
          this.iptComercial = false;
        }

        //Alimenta o formulario de dados pessoais
        this.iptPai = ficha.nome_pai;
        this.iptMae = ficha.nome_mae;
        this.iptEstadoCivil = ficha.estado_civil;
        this.iptDoador = ficha.doador_orgaos;
        this.iptSexo = ficha.sexo;
      } catch (e) {
        this.$alert({type: 'error', text: 'Ocorreu um erro tentar carregar seus dados. Contate o CRECI.'});
        // eslint-disable-next-line no-console
        console.log(e);
        await this.$router.replace('/recad');
      } finally {
        this.loading = false;
      }
    },
    async assinarTermo() {
      this.savingAssinaturaTermo = true;
      const appWebClient = new AppWebClient();
      try {
        await appWebClient.setPoliticaAssinatura(this.termo.id);
        this.termoOk = true;
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
      } finally {
        this.savingAssinaturaTermo = false;
      }
    },
    parseDate(datetime) {return moment(datetime).format('DD/MM/YYYY')},
    beautifyCpf(cpf) {return StringHelper.beautifyCpf(cpf)},
    ruleEmailRepeat(v) {
      return this.iptEmails.filter(e => e.trim() === v.trim()).length <= 1 || 'E-mail repetido';
    },
    getTelefoneIcon(tipo) {
      switch (tipo) {
        case 1:
          return {text: 'Residencial', value: 'mdi-home'};
        case 2:
          return {text: 'Comercial', value: 'mdi-office-building'};
        case 3:
          return {text: 'Celular particular', value: 'mdi-cellphone'};
        default:
      }
    },
    async buscarCepResidencial(v) {
      if (v.length !== 8) return;
      this.dialogLoadingCep = true;
      const endereco = await CepWebClient.smart(v);
      if (!endereco) {
        this.dialogLoadingCep = false;
        return;
      }
      this.iptUf = endereco.estado.toUpperCase();
      this.iptCidade = endereco.cidade.toUpperCase();
      this.iptBairro = endereco.bairro.toUpperCase();
      this.iptLogradouro = endereco.logradouro.toUpperCase();
      this.dialogLoadingCep = false;
    },
    async buscarCepComercial(v) {
      if (v.length !== 8) return;
      this.dialogLoadingCep = true;
      const endereco = await CepWebClient.smart(v);
      if (!endereco) {
        this.dialogLoadingCep = false;
        return;
      }
      this.iptComercialUf = endereco.estado.toUpperCase();
      this.iptComercialCidade = endereco.cidade.toUpperCase();
      this.iptComercialBairro = endereco.bairro.toUpperCase();
      this.iptComercialLogradouro = endereco.logradouro.toUpperCase();
      this.dialogLoadingCep = false;
    },

    selecionarNovaFoto() {
      this.dialogRegrasFoto = false;
      if (appconfig.mobile) {
        this.dialogMobileFoto = true;
      } else {
        this.$refs['file-foto'].click();
      }
    },
    async updateFoto() {
      //Metodo usado somente na versao Web, aps selecionar o arquivo.
      if (this.$refs['file-foto'].files.length === 0) {
        this.$alert({type: 'warning', text: 'Nenhum arquivo selecionado'});
        return;
      }
      const arquivo = this.$refs['file-foto'].files[0];
      this.$refs['file-foto'].value = ''; //zera o input

      const canvas = await loadImageAsync(arquivo, {
        maxWidth: 1080,
        maxHeight: 1920,
        orientation: true,
        canvas: true
      });
      this.dialogCrop = true;
      await this.$nextTick();
      this.$refs['croppie-foto'].bind({ url: canvas.toDataURL('image/jpeg', .78) });
    },
    async updateFotoMobile(galeria = true) {
      //Metodo usado somente na versao Mobile, abre a galeria ou a camera e ento obtem o base64 da foto.
      this.dialogLoading = true;
      const successCallback = async (imageData) => {
        const imageBlob = ImageHelper.dataURItoBlob('data:image/jpeg;base64,' + imageData);
        this.dialogMobileFoto = false;
        try {
          const canvas = await loadImageAsync(imageBlob, {
            maxWidth: 1080,
            maxHeight: 1920,
            orientation: true,
            canvas: true
          });
          this.dialogLoading = false;
          this.dialogCrop = true;
          await this.$nextTick();
          this.$refs['croppie-foto'].bind({ url: canvas.toDataURL('image/jpeg', .78) });
        } catch (e) {
          this.dialogLoading = false;
          this.$alert({type: 'error', text: 'Ocorreu um erro para processar a imagem'});
        }
      };
      const errorCallback = (message) => {
        this.dialogLoading = false;
        if (message && message !== 'No Image Selected') this.$alert({type: 'error', text: 'Ocorreu um erro para processar a imagem'});
      };
      const options = {
        // eslint-disable-next-line no-undef
        destinationType: Camera.DestinationType.DATA_URL,
        // eslint-disable-next-line no-undef
        sourceType: galeria ? Camera.PictureSourceType.PHOTOLIBRARY : Camera.PictureSourceType.CAMERA,
        // eslint-disable-next-line no-undef
        encodingType: Camera.EncodingType.JPEG,
        // eslint-disable-next-line no-undef
        mediaType: Camera.MediaType.PICTURE,
        // eslint-disable-next-line no-undef
        cameraDirection: Camera.Direction.FRONT,
        allowEdit: false,
        correctOrientation: true,
        quality: 50,
        saveToPhotoAlbum: true,
        targetWidth: 1080,
        targetHeight: 1920
      };
      navigator.camera.getPicture(successCallback, errorCallback, options);
    },
    async processarCroppie() {
      this.$loading('Processando imagem.<br/>Isso pode demorar um pouco.');
      const config = {
        type: 'base64',
        size: {width: 300, height: 400},
        format: 'jpeg',
        quality: .78
      };
      this.$refs['croppie-foto'].result(config, saida => {
        //Tratativa ps-crop
        let imagem = new Image();
        imagem.onload = async () => {
          try {
            //========= TESTE DE RECONHECIMENTO FACIAL ==========//
            //Carrega a rede neural SsdMobileNetV1, somente se j no estiver pronta.
            if (appconfig.mobile) {
              // === PLATAFORMA MOBILE ===
              if (!faceapi.nets.ssdMobilenetv1.isLoaded) await this.loadFaceDetectModels();
            } else {
              // === PLATAFORMA WEB ===
              if (!faceapi.nets.ssdMobilenetv1.isLoaded) await faceapi.nets.ssdMobilenetv1.loadFromUri('js/faceapi');
            }
            const deteccao_facial = await faceapi.detectSingleFace(imagem);
            //========= TESTE DE COR PREDOMINANTE DE FUNDO - BRANCO ============//
            const canvas_esquerdo = document.createElement('canvas');
            const canvas_direito = document.createElement('canvas');
            canvas_esquerdo.width = canvas_direito.width = 22;
            canvas_esquerdo.height = canvas_direito.height = 200;
            const contexto_esquerdo = canvas_esquerdo.getContext('2d');
            const contexto_direito = canvas_direito.getContext('2d');
            contexto_esquerdo.drawImage(imagem, 0, 0);
            contexto_direito.drawImage(imagem, -278, 0);
            let img_esquerda = new Image();
            img_esquerda.src = canvas_esquerdo.toDataURL("image/jpeg", 1);
            let img_direita = new Image();
            img_direita.src = canvas_direito.toDataURL("image/jpeg", 1);
            do {
              await this.$sleep(500);
            } while (!img_esquerda.complete || !img_direita.complete);
            const colorThief = new ColorThief();
            const lado_esquerdo_ok = colorThief.getPalette(img_esquerda) ? Math.min(...colorThief.getColor(img_esquerda)) >= 205 : true;
            const lado_direito_ok = colorThief.getPalette(img_direita) ? Math.min(...colorThief.getColor(img_direita)) >= 205 : true;
            const deteccao_fundo = lado_esquerdo_ok && lado_direito_ok;
            //Feedback
            this.$loading(false);
            if (!deteccao_fundo && deteccao_facial) {
              this.$alert({type: 'error', text: 'O fundo no  branco ou no possui claridade o suficiente, tente outra imagem ou mude o recorte'});
            } else if (deteccao_fundo && !deteccao_facial) {
              this.$alert({type: 'error', text: 'No reconhecemos a posio do rosto, tente outra imagem'});
            } else if (!deteccao_fundo && !deteccao_facial) {
              this.$alert({type: 'error', text: 'No reconhecemos a posio do rosto e a claridade da imagem, tente outra'});
            } else {
              this.iptFoto = saida;
              this.$alert({type: 'success', border: 'left', coloredBorder: true, text: 'Imagem aprovada'});
            }
          } catch (e) {
            // eslint-disable-next-line no-console
            console.log(e);
          } finally {
            this.dialogCrop = false;
            this.$loading(false);
          }
        };
        imagem.src = saida;
      });
    },
    async loadFaceDetectModels() {
      let MODEL_URL;
      if (window.device.platform === "Android") {
        MODEL_URL = window.cordova.file.applicationDirectory + "www/js/faceapi";
        faceapi.env.monkeyPatch({
          Canvas: HTMLCanvasElement,
          Image: HTMLImageElement,
          ImageData: ImageData,
          Video: HTMLVideoElement,
          createCanvasElement: () => document.createElement('canvas'),
          createImageElement: () => document.createElement('img'),
          readFile: filePath => new Promise(resolve => {
            window.resolveLocalFileSystemURL(
              filePath,
              function(fileEntry) {
                fileEntry.file(
                  function(file) {
                    const reader = new FileReader();
                    let fileExtension = filePath
                      .split('?')[0]
                      .split('.')
                      .pop();
                    if (fileExtension === 'json') {
                      reader.onloadend = function() {
                        resolve(this.result);
                      };
                      reader.readAsText(file);
                    } else {
                      reader.onloadend = function() {
                        resolve(new Uint8Array(this.result));
                      };
                      reader.readAsArrayBuffer(file);
                    }
                  },
                  function() {
                    resolve(false);
                  }
                );
              },
              function() {
                resolve(false);
              }
            );
          }),
        });
        await faceapi.nets.ssdMobilenetv1.loadFromDisk(MODEL_URL);
      } else {
        MODEL_URL = "./js/faceapi";
        await faceapi.loadSsdMobilenetv1Model(MODEL_URL);
      }
    },

    selecionarNovaAssinatura() {
      this.dialogAssinatura = false;
      if (appconfig.mobile) {
        this.dialogMobileAssinatura = true;
      } else {
        this.$refs['file-assinatura'].click();
      }
    },
    async updateAssinatura() {
      //Metodo usado somente na versao Web, aps selecionar o arquivo.
      if (this.$refs['file-assinatura'].files.length === 0) {
        this.$alert({type: 'warning', text: 'Nenhum arquivo selecionado'});
        return;
      }
      const arquivo = this.$refs['file-assinatura'].files[0];
      this.$refs['file-assinatura'].value = ''; //zera o input

      const canvas = await loadImageAsync(arquivo, {
        maxWidth: 1024,
        maxHeight: 720,
        orientation: true,
        canvas: true
      });
      this.iptAssinaturaTmp = canvas.toDataURL('image/png');
      this.dialogAssinaturaRaster = true;
      await this.$nextTick();
      await this.rasterizarAssinatura();
    },
    async updateAssinaturaMobile(galeria = true) {
      //Metodo usado somente na versao Mobile, aps selecionar o arquivo.
      this.dialogLoading = true;
      const successCallback = async (imageData) => {
        const imageBlob = ImageHelper.dataURItoBlob('data:image/jpeg;base64,' + imageData);
        this.dialogMobileAssinatura = false;
        try {
          const canvas = await loadImageAsync(imageBlob, {
            maxWidth: 1024,
            maxHeight: 720,
            orientation: true,
            canvas: true
          });
          this.iptAssinaturaTmp = canvas.toDataURL('image/jpeg', .78);
          this.dialogLoading = false;
          this.dialogAssinaturaRaster = true;
          await this.$nextTick();
          await this.rasterizarAssinatura();
        } catch (e) {
          this.dialogLoading = false;
          this.$alert({type: 'error', text: 'Ocorreu um erro para processar a imagem'});
        }
      };
      const errorCallback = (message) => {
        this.dialogLoading = false;
        if (message && message !== 'No Image Selected') this.$alert({type: 'error', text: 'Ocorreu um erro para processar a imagem'});
      };
      const options = {
        // eslint-disable-next-line no-undef
        destinationType: Camera.DestinationType.DATA_URL,
        // eslint-disable-next-line no-undef
        sourceType: galeria ? Camera.PictureSourceType.PHOTOLIBRARY : Camera.PictureSourceType.CAMERA,
        // eslint-disable-next-line no-undef
        encodingType: Camera.EncodingType.JPEG,
        // eslint-disable-next-line no-undef
        mediaType: Camera.MediaType.PICTURE,
        // eslint-disable-next-line no-undef
        cameraDirection: Camera.Direction.BACK,
        allowEdit: false,
        correctOrientation: true,
        quality: 50,
        saveToPhotoAlbum: true,
        targetWidth: 1080,
        targetHeight: 1920
      };
      navigator.camera.getPicture(successCallback, errorCallback, options);
    },
    async rasterizarAssinatura() {
      let canvas = await loadImageAsync(this.iptAssinaturaTmp, {
        maxWidth: 800,
        maxHeight: 450,
        orientation: true,
        canvas: true
      });
      const level = Math.abs(this.iptRasterLevel) * 10;
      canvas = ImageHelper.removerCorDeFundo(canvas, [level, level, level]);
      canvas = ImageHelper.trimTransparentBackground(canvas);
      canvas = ImageHelper.removeAlphaColor(canvas);
      this.iptAssinaturaTmpRaster = canvas.toDataURL('image/png');
    },

    async avancarPasso1() {
      //Higieniza os campos:
      this.iptEmails = this.iptEmails.filter(i => !!i.trim()); //Elimina e-mail vazio
      this.iptTelefones = this.iptTelefones.filter(i => StringHelper.extractNumbers(i.numero).length > 0); //Elimina telefone vazio
      await this.$nextTick();

      //Valida os campos:
      if (!this.$refs['form-1'].validate()) {
        this.$alert({text: 'Corrija os campos indicados em vermelho', type: 'warning', dismissible: true, timeout: 2500});
        return;
      }
      if (this.iptEmails.length ===  0) {
        this.$alert({text: 'Insira pelo menos 1 e-mail', type: 'warning', dismissible: true, timeout: 2500});
        this.iptEmails = [''];
        return;
      }
      const celular = this.iptTelefones.find(t => t.tipo === 3 || (t.tipo === 2 && StringHelper.extractNumbers(t.numero).substr(2, 1) === '9'));
      if (!celular) {
        this.$alert({text: 'Voc precisa informar pelo menos um telefone do tipo celular', type: 'warning', dismissible: true, timeout: 2500});
        return;
      }
      this.stepper = 2;
    },
    avancarPasso2() {
      //Valida os campos:
      if (!this.$refs['form-2'].validate()) {
        this.$alert({text: 'Corrija os campos indicados em vermelho', type: 'warning', dismissible: true, timeout: 2500});
        return;
      }
      this.stepper = 3;
    },
    avancarPasso3() {
      //Valida os campos:
      if (!this.$refs['form-3'].validate()) {
        this.$alert({text: 'Corrija os campos indicados em vermelho', type: 'warning', dismissible: true, timeout: 2500});
        return;
      }
      this.stepper = 4;
    },
    avancarPasso4() {
      //Valida foto:
      if (!this.iptFoto && !this.$store.state.ficha.foto) {
        this.$alert({type: 'error', text: 'Voc precisa inserir uma foto', timeout: 2500});
        return;
      }
      this.stepper = 5;
    },
    avancarPasso5() {
      //Valida assinatura:
      if (!this.iptAssinatura && !this.$store.state.ficha.assinatura) {
        this.$alert({type: 'error', text: 'Voc precisa inserir uma assinatura', timeout: 2500});
        return;
      }
      this.stepper = 6;
    },
    async finalizarRecadastramento() {
      const appWebClient = new AppWebClient();
      this.$loading(true);
      try {
        const endereco = {cep: this.iptCep, uf: this.iptUf, cidade: this.iptCidade, bairro: this.iptBairro, logradouro: this.iptLogradouro, numero: this.iptNumero, complemento: this.iptComplemento};
        const enderecoComercial = !this.iptComercial ? null : {cep: this.iptComercialCep, uf: this.iptComercialUf, cidade: this.iptComercialCidade, bairro: this.iptComercialBairro, logradouro: this.iptComercialLogradouro, numero: this.iptComercialNumero, complemento: this.iptComercialComplemento};
        const foto = ImageHelper.dataURItoBlob(this.iptFoto ? this.iptFoto : this.$store.state.ficha.foto);
        const assinatura = ImageHelper.dataURItoBlob(this.iptAssinatura ? this.iptAssinatura : this.$store.state.ficha.assinatura);
        await appWebClient.insertRecadastramento(this.iptEmails, this.iptTelefones, endereco, this.iptPai, this.iptMae, this.iptEstadoCivil, this.iptFormacao, this.iptDoador, this.iptSexo, foto, assinatura, enderecoComercial, this.iptCorrespondencia);
        await this.$store.dispatch('ficha/reloadAll');
        this.$alert({type: 'success', text: 'Recadastramento realizado com sucesso', coloredBorder: true, border: 'left'});
        await this.$router.push('/');
      } catch (e) {
        // eslint-disable-next-line no-console
        console.log(e);
      } finally {
        this.$loading(false);
      }
    },
  },
  created() {
    this.loadData();
  },
  watch: {
    dadosEssenciaisOk() {
      window.scrollTo(0, 0);
    },
    stepper() {
      window.scrollTo(0, 0);
    },
    iptComercial(v) {
      if (v === false) this.iptCorrespondencia = 1;
    },
    iptRasterLevel() {this.rasterizarAssinatura()},
  }
}
</script>

